.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background: url(../images/miami-new.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 1px;

  @media (min-width: 1400px) {
    padding-left: calc((100% - 1400px) / 2);
    padding-right: calc((100% - 1400px) / 2);
  }

  &__title {
    min-width: 300px;
    font-size: 80px;
    font-weight: 700;
    padding: 6% 9%;
    color: white;

    @media (max-width: 920px) {
      font-size: 46px;
      line-height: 40px;
    }
  }
}

.container-header__menu {
  padding: 0;
}

.container-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  gap: 21%;
  padding: 25px;
  min-width: 320px;
  height: min-content;

  &__phone {
    position: relative;
    align-items: center;
    margin-top: 31px;
    gap: 20px;
    cursor: pointer;
  }

  &__crown {
    z-index: 2;
    width: 135px;
    height: 76px;
    cursor: pointer;

    @media (max-width: 920px) {
      margin-top: 15px;
      width: 78px;
      height: 46px;
    }
  }
}

.phone__number {
  position: absolute;
  width: max-content;
  text-decoration: none;
  color: white;
  opacity: 0;
  transition: 1.5s;
  right: 40px;
  top: 3px;

  @media (max-width: 920px) {
    font-size: 10;
    right: 16%;
  }
}

.phone:hover .phone__number {
  opacity: 1;
  margin-bottom: 10px;
}

.menu__list {
  position: absolute;

}

.menu__img {
  position: absolute;
  width: 24px;
  height: 16px;
  margin-top: 31px;
  padding-left: 7%;
  background-image: url(../images/menu.png );
  background-repeat: no-repeat;
  transition: 0.5s;
  z-index: 2;
  cursor: pointer;

  &:hover {
    background-image: url(../images/menu-hover.png);
    background-repeat: no-repeat;
    transition: 1s;
  }

  @media (max-width: 920px) {
    width: 18px;
    height: 18px;
    margin-top: 31px;
    background-image: url(../images/menu.png);
    background-repeat: no-repeat;
  }
}

.phone__icon {
  @media (max-width: 920px) {
    width: 18px;
    height: 18px;
  }
}
