.menu__list {
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: rgba(0, 117, 255, 1);
  height: 100vh;
  width: 100%;
  left: 0;
  top: 0;
  font-weight: 700;
  opacity: 0;
  z-index: 1;
  transition: 1s;
  transform: translateY(-100%);

  @media (min-width: 1100px) {
    padding-left: calc((100% - 1400px) / 2);
    padding-right: calc((100% - 1400px) / 2);
  }

  & :nth-child(n) {
    text-decoration: none;
    font-size: 22px;
    line-height: 24px;
    color: rgba(255, 255, 255, 1);
    margin-top: 32px;
    margin-left: 10%;
  }

  & :first-child {
    margin-top: auto;
  }

  & :nth-child(7) {
    margin-top: auto;
    line-height: 16px;
    font-size: 16px;
  }

  & :last-child {
    margin-bottom: auto;
    text-decoration: underline;
    line-height: 16px;
    font-size: 12px;
  }

  & :nth-child(n):hover {
    text-decoration: underline;
    cursor: pointer;
  }

  &--active {
    opacity: 1;
    transform: translateY(0);
  }
}
