.footer {
  position: relative;
  height: 360px;
  background-image: url(../images/footer.jpg);
  object-fit: cover;
  background-repeat: no-repeat;
  padding-top: 243px;
  background-size: cover;
  background-position: 50%;

  @media (max-width: 800px) {
    height: 190px;
  }
}

.footer__img {
  width: 115px;
  position: absolute;
  left: calc(50% - (115px / 2));
  bottom: 40px;

  @media (max-width: 800px) {
    bottom: 50px;
    width: 70px;
    left: calc(50% - (70px / 2));
  }
}
