.testimonials {
  display: flex;
  flex-direction: column;
  margin-bottom: 152px;

  @media (min-width: 1400px) {
    padding-left: calc((100% - 1250px) / 2);
    padding-right: calc((100% - 1250px) / 2);
  }
}

.testimonials__title {
  text-align: center;
  font-size: 3rem;

  @media (max-width: 999px) {
    font-size: 1.5rem;
  }
}

.testimonials__logo {
  width: 3.5%;
  margin: -1% auto;
}

.testimonials__contain {
  display: flex;
  text-align: center;
  align-items: center;
  line-height: 27px;
  margin-top: 4%;

  @media (max-width: 999px) {
    font-size: 14px;
    line-height: 23px;
  }

  @media (max-width: 620px) {
    flex-direction: column;
    margin-left: calc((100% - 300px) / 2);
    margin-right: calc((100% - 300px) / 2);
  }
}

.contain__person {
  gap: 5%;
  width: 50%;

  @media (max-width: 620px) {
    width: auto;
  }
}

.person__foto {
  width: 153px;
}

.person__full-name {
  padding: 2%;
  font-size: 1.5rem;
  color: rgba(0, 117, 255, 1);

  @media (max-width: 999px) {
    font-size: 1.3rem;
  }
}

.person__profesion {
  font-size: 0.9em;
  color: rgb(142, 145, 148);
  font-weight: normal;
}

.person__text {
  font-size: 1em;
  margin: 5% 10%;

  @media (max-width: 620px) {
    margin: 15px 10px;
  }
}
