.contacts {
  display: block;
  padding: 0 120px 88px;

  @media (min-width: 1400px) {
    padding-left: calc((100% - 1250px) / 2);
    padding-right: calc((100% - 1250px) / 2);
  }
}

.contacts__title {
  text-align: center;
  font-size: 3rem;

  @media (max-width: 999px) {
    font-size: 1.5rem;
  }
}

.contacts__img {
  display: block;
  width: 3.5%;
  margin: -1% auto 0;
}

.contacts__container-links {
  display: flex;
  justify-content: center;
  gap: 190px;
  padding-top: 77px;

  @media (max-width: 1070px) {
    flex-direction: column;
    gap: 32px;
    align-items: center;
    padding-top: 37px;
  }
}

.container-links__box {
  display: flex;
  gap: 108px;

  @media (max-width: 1070px) {
    flex-direction: column;
    gap: 24px;
    width: 300px;
  }
  
  @media (max-width: 500px) {
    gap: 24px;
    width: 200px;
  }
}

.forms :nth-child(-n + 2) {
  height: 48px;
  padding: 17px;
  outline: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(255, 255, 255, 1);

  &:hover {
    border: 2px solid rgba(0, 94, 204, 1);
  }
}

.container-links__forms {
  display: flex;
  flex-direction: column;
  gap: 16px;

  
  @media (max-width: 1070px) {
    width: 300px;
  }

  @media (max-width: 500px) {
    gap: 24px;
    width: 200px;
  }
}

.forms__textarea {
  padding: 17px;
  width: 413px;
  height: 144px;
  outline: none;
  border-radius: 8px;
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(255, 255, 255, 1);

  &:hover {
    border: 2px solid rgba(0, 94, 204, 1);
  }

  @media (max-width: 1070px) {
    width: 300px;
  }

  @media (max-width: 500px) {
    gap: 24px;
    width: 200px;
  }
}

.box__text {
  font-size: 14px;
  color: #8e9194;

  & + * {
    color: rgba(62, 62, 62, 1);
  }
}

.box__links-forms {
  font-size: 16px;
  line-height: 27px;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: rgba(0, 94, 204, 1);
  }
}

.forms__submit {
  font-size: 16px;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  height: 48px;
  margin-top: 10px;
  outline: none;
  cursor: pointer;
  border: 1px rgba(0, 94, 204, 1);
  transition: 3s ease-in-out;
  border-radius: 8px;
  box-shadow: 0 5px 10px rgba(0, 83, 181, 0.46);
  background-color: rgba(0, 94, 204, 1);

  &:hover {
    box-shadow: 0 15px 27px rgba(0, 83, 181, 0.46);
  }

  @media (max-width: 1070px) {
    width: 300px;
  }

  @media (max-width: 500px) {
    gap: 24px;
    width: 200px;
  }
}
