.gallery {
  display: flex;
  flex-direction: column;
  padding: 0 120px 152px;
  margin: 1px;
  justify-content: center;

  @media (min-width: 1400px) {
    padding-left: calc((100% - 1250px) / 2);
    padding-right: calc((100% - 1250px) / 2);
  }

  @media  (max-width: 999px) {
    padding: 0 60px 75px;
  }

  @media  (max-width: 500px) {
    padding: 0 20px 64px;
  }
}

.gallery__title {
  text-align: center;
  font-size: 48px;
  line-height: 48px;

  @media (max-width: 936px) {
    padding-top: 5%;
  }
}

.gallery__logo {
  width: 3.5%;
  margin: -1% auto;
}

.gallery__container {
  display: flex;
  flex-direction: column;
  margin-top: 45px;

  @media (max-width: 935px) {
    padding-top: 5%;
    margin: 10px;
  }
}

.container__img {
  display: flex;
  gap: 20px;

  & :nth-child(n) {
    border-radius: 8px;
    height: 30vh;
    object-fit: cover;
  }
}

.img__big.img__big.img__big {
  width: 63%;
  flex-grow: 2;

  @media (max-width: 650px) {
    width: 50%;
    height: 20vh;
  }
}

.img__middle.img__middle {
  width: 50%;

  @media (max-width: 650px) {
    height: 20vh;
  }
}

.img__small.img__small {
  width: 37%;
  flex-grow: 1;

  @media (max-width: 650px) {
    width: 50%;
    height: 20vh;
  }
}

.container__text {
  font-size: 16px;
  line-height: 27px;
  margin: 2% 0 1%;
}

.text__link {
  font-weight: 700;
  color: rgba(0, 117, 255, 1);
  text-decoration: none;
}
